import React from "react";
import ReactGA from "react-ga";
import { CTAComponent, Button } from "./styles/StyledLetterCTA";

class LetterCTA extends React.Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick() {
    if (this.props.tracking.active) {
      ReactGA.event({
        category: this.props.tracking.category,
        action: "Download_Resume" + this.props.tracking.devState
      });
    }
    window.open(this.props.cta.URL);
  }
  render() {
    return (
      <CTAComponent color= "#ec546b">
        <div>
          <Button onClick={this.handleClick}>
            {this.props.cta.buttonName}
          </Button>
        </div>
      </CTAComponent>
    );
  }
}
export default LetterCTA;
