import React from "react";
import CoverLetter from "./components/CoverLetter";
import "./App.css";
import styled from "styled-components";

function App() {
  const arr = [1, 2, 3, 3, 2, 1, 2, 1, 1];
  const obj = { 1: 0, 2: 0, 3: 0 };

  for (let i = 0; i < arr.length; i++) {
    if (arr[i] === 1) {
      obj[1]++;
    }
    if (arr[i] === 2) {
      obj[2]++;
    }
    if (arr[i] === 3) {
      obj[3]++;
    }
  }
  console.log(obj);

  return (
    <AppDiv>
      <AppHeaderHeader>
        <div>
          <CoverLetter />
        </div>
      </AppHeaderHeader>
    </AppDiv>
  );
}
export default App;

const AppDiv = styled.div`
  background-color: #ffffff;
`;

const AppHeaderHeader = styled.header`
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-family: Helvetica, Arial, sans-serif;
  color: #cccccc;
  height: auto;
  margin: 0 auto;
  display: block;
`;
