import React from "react";
import { BodyContainerComponent } from "./styles/StyledLetterBody";

class LetterBody extends React.Component {
  constructor(props) {
    super(props);
    this.createBody = this.createBody.bind(this);
  }
  createBody() {
    return this.props.copy.map((bodyCopy, index) => (
      <p key={index}>{bodyCopy}</p>
    ));
  }
  render() {
    return (
      <BodyContainerComponent>
        <div>{this.createBody()}</div>
      </BodyContainerComponent>
    );
  }
}
export default LetterBody;
