import React from "react";
import {
  SenderCardComponentDiv,
  SideByDiv,
  SenderPic,
  SenderTitle,
  SenderSubTitle,
  SenderSubSubTitle
} from "./styles/StyledSenderCard";

class SenderCard extends React.Component {
  render() {
    return (
      <SenderCardComponentDiv>
        <SideByDiv>
          <SenderPic src={this.props.profilePic} alt={this.props.sender} />
        </SideByDiv>
        <SideByDiv>
          <SenderTitle>{this.props.sender}</SenderTitle>
          <SenderSubTitle>{this.props.senderTitle}</SenderSubTitle>
          <SenderSubSubTitle>{this.props.senderLocation}</SenderSubSubTitle>
        </SideByDiv>
      </SenderCardComponentDiv>
    );
  }
}
export default SenderCard;
