import React from "react";
import LetterData from "../data/LetterData";
import LetterHeader from "./LetterHeader";
import LetterSalutation from "./LetterSalutation";
import LetterCTA from "./LetterCTA";
import LetterBodyContainer from "./LetterBodyContainer";
import ReactGA from "react-ga";
import {CoverLetterComponent } from "./styles/StyledCoverLetter";
class CoverLetter extends React.Component {
  componentDidMount() {
    if (LetterData.tracking.active) {
      ReactGA.initialize(LetterData.tracking.id);
      ReactGA.pageview(window.location.pathname + window.location.search);
      ReactGA.event({
        category: LetterData.tracking.category,
        action: "Load_The_Letter" + LetterData.tracking.devState
      });
    }
  }
  render() {
    return (
      <div>
        <CoverLetterComponent>
          <LetterHeader {...LetterData} />
          <LetterBodyContainer {...LetterData} />
          <LetterSalutation {...LetterData} />
          <LetterCTA {...LetterData} />
        </CoverLetterComponent>
      </div>
    );
  }
}
export default CoverLetter;
