import React from "react";
import {
  InlineDiv,
  GreetingName,
  GreetingComponent,
  GreetingTitle,
  CompanyLogoDiv,
  CompanyLogoPic
} from "./styles/StyledLetterGreeting";

class LetterGreeting extends React.Component {
  render() {
    let greetingConcat;
    if (
      this.props.greetingImage === "" ||
      this.props.greetingImage === undefined
    ) {
      greetingConcat = (
        <InlineDiv>
          <div>{this.props.greetingName}</div>
        </InlineDiv>
      );
    } else {
      greetingConcat = (
        <div>
          <InlineDiv>
            <GreetingName>{this.props.greetingName}</GreetingName>
          </InlineDiv>
        </div>
      );
    }
    return (
      <GreetingComponent>
        <InlineDiv>
          <GreetingTitle>{this.props.greeting}</GreetingTitle>
        </InlineDiv>
        <InlineDiv>{greetingConcat}</InlineDiv>
        <InlineDiv>
          <GreetingTitle>{","}</GreetingTitle>
        </InlineDiv>
        <CompanyLogoDiv>
          <CompanyLogoPic
            src={this.props.greetingImage}
            alt={this.props.greetingName}
          />
        </CompanyLogoDiv>
      </GreetingComponent>
    );
  }
}
export default LetterGreeting;
