import React, { useState, useRef } from "react";
import ReactGA from "react-ga";

import "./styles/Accordion.css";

function Accordion(props) {
  const [setActive, setActiveState] = useState("");
  const [setHeight, setHeightState] = useState("0px");
  const [setRotate, setRotateState] = useState("accordion__icon");
  const content = useRef(null);
  const contentText = useRef(null);
  const contentImage = useRef(null);

  function toggleAccordion() {
    setActiveState(setActive === "" ? "active" : "");

    let thisAction;
    if (setActive === "") {
      thisAction = "opening";
    } else {
      thisAction = "closing";
    }

    ReactGA.event({
      category: props.tracking.category,
      action: props.spotLight.title + "_" + thisAction + props.tracking.devState
    });

    setHeightState(
      setActive === "active" ? "0px" : `${content.current.scrollHeight}px`
    );
    setRotateState(
      setActive === "active" ? "accordion__icon" : "accordion__icon rotate"
    );
  }

  return (
    <div className="accordion__section">
      <div className={`accordion ${setActive}`}>
        <h1 ref={contentText} className="acordionTitle">
          {props.spotLight.title}
        </h1>
        <img
          ref={contentImage}
          src={props.spotLight.spotlightPic}
          className="spotlightPic "
          alt={props.spotLight.spotlightPicDescription}
        />
        <img
          src={props.spotLight.spotlightMinusPic}
          className={`${setRotate}`}
          alt={"minus button"}
        />
        <img
          src={props.spotLight.spotlightMinusPic}
          className={`accordion__icon rotate`}
          alt={"minus button"}
          onClick={toggleAccordion}
        />
      </div>
      <div
        ref={content}
        style={{ maxHeight: `${setHeight}` }}
        className="accordion__content"
      >
        <div className="spotlightContent">
          <div className="spotlightTitle">
            <span>{props.spotLight.spotlightTitle}</span>
          </div>
          <div className="spotlightDescription">
            <span>{props.spotLight.spotlightDesciption}</span>
          </div>
          <div className="spotlightLinkHolder">
            <a
              className="spotlightLink"
              href={props.spotLight.spotlightLink}
              target="blank"
            >
              {props.spotLight.spotlightLinkDesciption}
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Accordion;
